.btn {
  font-size: $fsButton;
  padding: $padBtnVer $padBtnHor;
  border-radius: 44px;
  border: 1px solid $bgButton;
  color: $colorTextButton;
  line-height: 24px;
  cursor: pointer;
  transition: background .1s linear;

  &-subscribe {
    @include fontFamily("Roboto");
    font-weight: 600;
    background-color: $bgButton;
    color: $colorTextButton;

    @include mdx($tablet-width) {
      font-size: $fsMSubscribeButton;
      padding: 12px 16px;
    }

    @include mdn($tablet-width + 1) {
      font-size: $fsDSubscribeButton;
      padding: 16px 20px;
    }
  }

  &-primary {
    background-color: $bgButton;
    transition: background .1s linear;

    &:hover {
      background-color: rgba($bgButton, 0.7);
      border: 1px solid rgba($bgButton, 0.7);
    }

    &:active {
      background-color: $bgButton;
      border: 1px solid $bgButton;
    }
  }

  &-secondary {
    background-color: #fff;
  }
}

.input {
  &-item {
    padding: $padBtnVer $padBtnHor;
    border: 1px solid $bgButton;
    border-radius: 40px;
    text-indent: 4px;
    font-size: inherit;
    line-height: 24px;
    min-width: 50px;
  }

  &-text {
    padding: $padBtnVer 2px;
    border-radius: 44px;
  }

  &-email {
    //padding: $padBtnVer 2px;
    border-radius: 44px;
  }

  &-numb {
    text-indent: 10px;
  }
}

.selectbox {
  font-size: inherit;
  width: 100%;
  max-width: 300px;

  border: 1px solid $bgButton;
  border-radius: 40px;
  padding-left: 12px;

  &:active,
  &.open {
    border: 1px solid $bgButton;
  }

  &:focus {
    border: 1px solid darken($bgButton, 30%);
  }

  .list {
    left: 0;
    right: 0;
    border: inherit;
  }
}